import React, { useState } from "react";
import SuperButton from "../hw04/common/c2-SuperButton/SuperButton";
import { restoreState } from "../hw06/localStorage/localStorage";
import s from "./Clock.module.css";

function Clock() {
  const [timerId, setTimerId] = useState<number | undefined>(undefined);
  // for autotests //   //      ,     
  const [date, setDate] = useState<Date>(
    new Date(restoreState("hw9-date", Date.now()))
  );
  const [show, setShow] = useState<boolean>(false);

  const start = () => {
    if (timerId !== undefined) return;

    const id = window.setInterval(() => {
      setDate(new Date());
    }, 1000);
    setTimerId(id);
    //   //   (   ,   +1)
    //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
  };

  const stop = () => {
    if (timerId !== undefined) {
      window.clearInterval(timerId);
    }
    setTimerId(undefined);
    //   //    ,    (timerId <- undefined)
  };

  const onMouseEnter = () => {
    setShow(true);
    //   //     
  };
  const onMouseLeave = () => {
    setShow(false);
    //   //      
  };

  let formatter1 = new Intl.DateTimeFormat("en", {
    weekday: "long",
  });

  let formatter2 = new Intl.DateTimeFormat("ru", {
    hour: "numeric",
    minute: "numeric",
    second: "numeric",
  });

  let formatter3 = new Intl.DateTimeFormat("en", {
    month: "long",
  });

  let formatter4 = new Intl.DateTimeFormat("ru", {
    day: "numeric",
    month: "numeric",
    year: "numeric",
  });

  const stringTime = formatter2.format(date) || <br />; // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
  const stringDate = formatter4.format(date) || <br />; // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

  //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
  const stringDay = formatter1.format(date) || <br />; //  
  const stringMonth = formatter3.format(date) || <br />; //  

  return (
    <div className={s.clock}>
      <div
        id={"hw9-watch"}
        className={s.watch}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
      >
        <span id={"hw9-day"}>{stringDay}</span>,{" "}
        <span id={"hw9-time"}>
          <strong>{stringTime}</strong>
        </span>
      </div>

      <div id={"hw9-more"}>
        <div className={s.more}>
          {show ? (
            <>
              <span id={"hw9-month"}>{stringMonth}</span>,{" "}
              <span id={"hw9-date"}>{stringDate}</span>
            </>
          ) : (
            <>
              <br />
            </>
          )}
        </div>
      </div>

      <div className={s.buttonsContainer}>
        <SuperButton
          id={"hw9-button-start"}
          disabled={timerId !== undefined} //   //    
          onClick={start}
        >
          start
        </SuperButton>
        <SuperButton
          id={"hw9-button-stop"}
          disabled={timerId === undefined} //   //     
          onClick={stop}
        >
          stop
        </SuperButton>
      </div>
    </div>
  );
}

export default Clock;
